@use "reset";
BODY {
  background-color: #171f3b;
  padding: 0;
  margin: 0;
  position: relative;
}
.background {
  z-index: 1;
  top: 0;
  position: fixed;
  height: 100vh;
  display: flex;
  align-items: center;
  @media (min-width: 860px) {
    display: block;
    align-items: first;
  }
  IMG {
    height: 80%;
    width: auto;
    position: relative;
    @media (min-width: 860px) {
      width: 100%;
      height: auto;
    }
  }
}

.main {
  z-index: 10;
  position: relative;
  width: 100%;
  max-width: 548px;
  min-height: 100vh;
  margin-right: auto;
  margin-left: auto;
  z-index: 3;
  background: rgba(255,255,255,0.9);
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.5);
  margin-top: 85vh;
}
@media (min-width: 860px) {
  .main {
    max-width: 548px;
    margin-right: 160px;
    margin-top: 24px;
  }
}
.container {
  padding: 20px 30px;
}
H1 {
  padding: 50px 0;
}
.content {
  &_title {
    position: relative;
    font-size: 18px;
    font-weight: bold;
    line-height: 1.4;
    padding:0.75em 1em;
    display: inline-block;
    width: 100%;
    text-align: center;
    color: #000;
    border-top: solid 3px #000;
    border-bottom: solid 3px #000;
    margin: 2rem 0 0 0;

    &:before {
      content:'';
      height: 60%;
      position: absolute;
      display: inline-block;
      border-left: solid 3px #000;
      top:0;
      left: 0;
    }
    &:after {
      content:'';
      height: 60%;
      position: absolute;
      display: inline-block;
      border-right: solid 3px #000;
      bottom:0;
      right: 0;
    }
    &_en {
      text-align: center;
      font-size: 16px;
      font-weight: bold;
      text-transform: uppercase;
    }
  }
}

P {
  line-height: 1.8;
  padding-bottom: 16px;
}
.text-center {
  text-align: center;
}

.datalist {
  display: flex;
  flex-wrap: wrap;
  DT {
    width: 50px;
    margin-right: 16px;
    margin-bottom: 16px;
  }
  DD {
    width: calc(100% - 66px);
    margin-bottom: 16px;
  }
}

.entry {
  background: rgba(0, 0, 0, 0.8);
  padding: 48px 24px;
  margin: 24px 0;
  text-align: center;
  border-radius: 4px;
}

.btn {
  font-size: 20px;
  font-style: italic;
  letter-spacing: 0.3rem;
  color: #fff;
  padding: 24px 24px 24px 48px;
  border: 5px solid #fff;
  border-radius: 1rem;
  text-decoration: none;
  display: inline-block;
  &-neon {
    animation: flicker 1.5s infinite alternate;
    text-decoration: none;
    font-weight: bold;
    line-height: 1;

    &:after {
      content: "â†’";
      margin-left: 24px;
    }
  }
  &-off {
    color: #999;
    border: 5px solid #999;
    padding: 24px 48px;
  }
}
/* Animate neon flicker */
@keyframes flicker {
  0%, 19%, 21%, 23%, 25%, 54%, 56%, 100% {
    text-shadow:
            -0.1rem -0.1rem 0.5rem #fff,
            0.1rem 0.1rem 0.5rem #fff,
            0 0 1rem #f40,
            0 0 2rem #f40,
            0 0 3rem #f40,
            0 0 4rem #f40,
            0 0 5rem #f40;

    box-shadow:
            0 0 .2rem #fff,
            inset 0 0 .2rem #fff,
            0 0 1rem #08f,
            inset 0 0 1rem #08f,
            0 0 2rem #08f,
            inset 0 0 2rem #08f;
  }
  20%, 24%, 55% {
    text-shadow: none;
    box-shadow: none;
  }
}

#caoution {
  border-radius: 4px;
  border: 3px solid #c00;
  padding: 16px 8px 4px 8px;
  color: #c00;;
  background: #fee;
}
.sponsor-list {
  margin: 0 0 24px 0;
  padding: 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 16px;
  LI {
    margin: 0;
    padding: 0;
    list-style: none;
    IMG {
      border: 1px solid #ccc;
      height: auto;
      width: 100%;
    }
  }
}
